<template>
    <div class="wmoney" :class="showWmoney?'active':''">
        <div class="title"><img class="close" @click="$emit('cance')" :src="ImgUrl+'home/close.png'" alt=""> 输入交易密码</div>
        <div class="content">
            <img v-if="show" :src="ImgUrl+'home/loading.gif'" alt="">
            <div v-if="!show" class="name">提现</div>
            <div v-if="!show" class="price"><span>￥</span>{{amount}}</div>
            <div v-if="!show" class="service">扣除{{ratioAmount}}元手续费</div>
            <div v-if="!show" class="pswK">
                <div v-for="(item,index) in keydatas" :key="index"><span v-if="item"></span></div>
            </div>
            <div v-if="!show" class="gopsw"><router-link to="/changePassword/123">忘记交易密码？</router-link></div>
        </div>
    </div>
</template>
<script>
import { walletWithdraw } from '@/api/withdraw.js'
import { Toast } from 'mint-ui';
export default {
    props:{
        showWmoney:{
            default:true
        },
        ratio:{
            default:0
        },
        pswValue:{
            default:''
        },
        invoiceExpressNo:{
            default:''
        },
        invoiceExpressCompany:{
            default:''
        },
        amount:{
            default:''
        }
    },
    data() {
        return {
            show:false, // 控制显示
            keydatas:['','','','','',''], // 输入值数组
            indexCon:0, // 记录当前输入值序列号
            withdrawPassword:'', // 提现密码
            ratioAmount:0,
        }
    },
    methods:{
        // 提现
        walletWithdraw() {
            walletWithdraw({
                invoiceExpressCompany: this.invoiceExpressCompany,
                invoiceExpressNo: this.invoiceExpressNo,
                amount: this.amount*1,
                withdrawPassword: this.withdrawPassword*1
            }).then(data=>{
                this.show = false 
                if(data.code==0){
                    Toast('提现成功');
                    this.$emit('cance')
                }else {
                    if(data.message == "提现密码错误"){
                        this.$emit('totlePsw')
                    }
                    
                }
                this.keydatas = ['','','','','','']
                this.indexCon = 0
                this.$forceUpdate();
                
            })
        }
    },
    mounted() {
    },
    watch:{
        amount:{
            handler(val) {
                if(val){
                    this.ratioAmount = val*this.ratio/100 
                    this.ratioAmount = this.ratioAmount.toFixed(2)
                }
            }
        },
        pswValue:{
            handler(val){
                if(val != -1){ // 值为非-1是进入
                    if(!val){
                        if(this.indexCon>0){ //删除时先减1找到当前删除索引
                            this.indexCon-- 
                        }
                        this.keydatas[this.indexCon] = val // 把当前索引值改为''
                        this.$emit('changepswValue') // 赋值完毕后将pswValue值改为-1，以免多次传入相同值时保护能触发watch
                        this.$forceUpdate(); // 手动刷新渲染
                    }else{
                        if(this.keydatas.length>this.indexCon){ // 判断当前索引小于数组长度
                            this.keydatas[this.indexCon] = val // 将值赋值给当前索引
                            this.$emit('changepswValue') // 赋值完毕后将pswValue值改为-1，以免多次传入相同值时保护能触发watch
                            this.$forceUpdate(); // 手动刷新渲染
                            this.indexCon++ // ++索引
                            if(this.indexCon==6){ // 当索引为6时 发送请求 请求成功重置
                                this.withdrawPassword = this.keydatas.join('') 
                                this.show = true 
                                this.walletWithdraw()
                            }
                        }
                    }
                }
                
            },
            deep:true,
            immediate: true
        }
    }
}
</script>
<style lang="less" scoped>
.wmoney{
    height: 5.3rem;
    width: 5.6rem;
    position: fixed;
    background-color: #fff;
    top: 3.18rem;
    left: 50%;
    transform: translateX(-50%) scale(0);
    overflow: hidden;
    border-radius: .1rem;
    transition: all .5s;
    .pswK{
        width: 100%;
        height: .8rem;
        padding: 0 .32rem;
        display: flex;
        margin-top:.4rem;
        div{
            flex: 1;
            border:1px solid #e5e5e5;
            position: relative;
            border-left: 0;
            span{
                position: absolute;
                width: .22rem;
                height: .22rem;
                background-color: #000000;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                border-radius: 50%;
            }
        }
        div:nth-of-type(1){
            border:1px solid #e5e5e5;
        }
    }
    .gopsw{
        font-family: MicrosoftYaHei;
        font-size: .24rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #1757a8;
        text-align: right;
        padding-right: .32rem;
        margin-top: .1rem;
    }
    .content{
        height: 4.3rem;
        width: 100%;
        position: relative;
        text-align: center;
        padding-top: .4rem;
        .service{
            	font-family: MicrosoftYaHei;
            font-size: .3rem;
            font-weight: normal;
            font-stretch: normal;
            letter-spacing: 0px;
            color: #999999;
            margin-top: .2rem;
        }
        .price{
            font-family: MicrosoftYaHei;
            font-size: .6rem;
            font-weight: normal;
            letter-spacing: 0px;
            color: #000000;
            margin-top: .2rem;
            span{
                font-size: .48rem;
            }
        }
        .name{
            font-family: MicrosoftYaHei;
            font-size: .3rem;
            font-weight: normal;
            font-stretch: normal;
            letter-spacing: 0px;
            color: #000000;
        }
        img{
            width: 4rem;
            height: 4rem;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%)
        }
    }
    .title{
        height: 1rem;
        width: 100%;
        line-height: 1rem;
        font-family: MicrosoftYaHei;
        font-size: .3rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #000000;
        position: relative;
        text-align: center;
        border-bottom:1px solid #e5e5e5;
        .close{
            position: absolute;
            width: .26rem;
            height: .26rem;
            top: 50%;
            left: .36rem;
            transform: translateY(-50%);
        }
    }
}
.wmoney.active{
    transform: translateX(-50%) scale(1);
    transition: all .5s;
}
</style>



