<template>
  <div id="withdrawal">
    <mt-header title="提现" style="height:.88rem;background-color:#4a57ff">
        <mt-button slot="left" icon="back" @click="goBack"></mt-button>
    </mt-header>
    <div class="bankInfo">
      <div class="bankCard">
        <div class="bankCardInfo">
          <div class="left"><img :src="ImgUrl+bankLogoUrl" alt=""></div>    
          <div class="right">
            <div>{{dataInfo.bankName}}</div>  
            <div>{{bankCardNo}}</div>  
            <div class="subBankName">开户行：{{dataInfo.subBankName}}</div>  
          </div>
        </div>  
      </div> 
      <div class="forgetPsw"><router-link to="/changePassword/123"><span>忘记提现密码</span></router-link></div> 
    </div> 
    <div class="withdrawal_money">
      <div class="title">提现金额</div>  
      <div>
        <input type="number" v-model="amount" @keyup='validationPrice($event)'>
        <span>￥</span>
      </div>
      <div class="amount" v-if="amount<=dataInfo.balance*1"><div>可提现金额{{dataInfo.balance}}，<span @click="allIn">全部提现</span></div>  <div class="amount2" v-if="amount">到账金额{{ratioAmount}}</div> </div>
      <div class="amount1" v-else>金额已超过可提现余额</div>
      <!-- <div class="amount" v-if="amount<=dataInfo.balance*1"></div> -->
    </div>
    <div class="express">
      <div v-if="this.dataInfo.bankCardType==1"><span>发票快递公司</span><input type="text" :class="invoiceExpressCompany?'active':''" v-model="invoiceExpressCompany" placeholder="请输入邮寄发票的快递公司名称"></div>
      <div v-if="this.dataInfo.bankCardType==1"><span>快递单号</span><input type="text" :class="invoiceExpressNo?'active':''" v-model="invoiceExpressNo" placeholder="请输入快递单号"></div>
    </div>    
    <div class="userInfo">
      <div><span>提现账户名</span><span>{{dataInfo.bankOpenName}}</span></div>
      <div><span>提现类型</span>
        <span v-if="dataInfo.bankCardType==1">公司</span>
        <span v-if="dataInfo.bankCardType==2">个人</span>
      </div>
    </div>
    <div class="rule_info">
      <div class="title"><img class="ts" :src="ImgUrl+'/home/ts.png'" alt=""> 温馨提示</div>
      <div style="margin-top:.1rem">1、提现手续费{{dataInfo.ratio}}%；</div>
      <div>2、提现需要邮寄发票，发票总面额至少要大于提现额度,否
则可能造成提现不成功， 发票模板点击查看；</div>
      <div>3、发票邮寄地址：</div>
      <div>广东省深圳市南山区桃园路田厦金牛广场A座4006大千生活；</div>
      <div>4、联系人：大千生活；</div>
      <div>5、提现成功发起后，7个工作日左右到账；</div>
      <div>6、每月10号、20号、30号才能提现；</div>
      <div>7、最低提现金额100元。</div>
      <mt-button type="primary" @click="submit" :disabled="dis" >确定</mt-button>
    </div>
    <div class="mrsk" v-if="showNoPsw||showWmoney"></div>
    <noPsw :showNoPsw="showNoPsw" @cance="showNoPsw=false;showWmoney=true"></noPsw>
    <wmoney :showWmoney = "showWmoney"  :pswValue = "pswValue" :amount="amount" :ratio="dataInfo.ratio" @totlePsw="showNoPsw=true;showWmoney=false" :invoiceExpressCompany="invoiceExpressCompany" :invoiceExpressNo="invoiceExpressNo" @cance="onsuccess" @changepswValue='pswValue=-1'></wmoney>
    <keyboard :show = "showWmoney" @typing="onkeyBoard"></keyboard>
  </div>
</template>
<script>
import noPsw from './components/noPsw.vue'
import wmoney from './components/wmoney.vue'
import { getWithdrawInfo } from '@/api/withdraw.js'
import keyboard from './components/keyboard.vue'
import { goBack, getTokus, getPhone } from '@/utils/native.js'
import bankLogo from '@/utils/bankName.js'
import { Toast } from 'mint-ui';
export default {
  data() {
    return {
      amount:'', //提现金额
      showNoPsw:false,  // 密码错误弹框
      showWmoney:false, // 输入密码弹框
      keyboard:null,  
      pswValue:'' , // 键盘返回值
      invoiceExpressCompany:'', // 发票邮寄公司
      invoiceExpressNo:'', // 发票邮寄单号
      dataInfo:{}, // 用户信息
      bankLogoUrl:'bankLogo/0.png',
      bankCardNo:'',
      ratioAmount:'',
      dis:''
    }
  },
  components: {
    noPsw,
    wmoney,
    keyboard
  },
  methods: {
    onsuccess() {
      this.showNoPsw=false;
      this.showWmoney=false
      this.getWithdrawInfo()
    },
    getWithdrawInfo() {
      getWithdrawInfo().then(data=>{
        this.dataInfo = data.data
        console.log('dataInfo'+this.dataInfo.bankName)
        bankLogo.forEach((item,index) => {
          if(this.dataInfo.bankName == item.name){
            this.bankLogoUrl = item.imgUrl
           
            
          } 
        })
        this.bankCardNo = this.dataInfo.bankCardNo.slice(0,6) + '**** ****' + this.dataInfo.bankCardNo.slice(this.dataInfo.bankCardNo.length-5,this.dataInfo.bankCardNo.length-1)
      })
    },
    // 验证金额
    validationPrice(e) {
      e.target.value=e.target.value.replace(/[^\d.]/g,"").replace(/\.{2,}/g,".").replace(".","$#$").replace(/\./g,"").replace("$#$",".").replace(/^(\-)*(\d+)\.(\d\d).*$/,"$1$2.$3")
      if(e.target.value.indexOf(".")< 0 && e.target.value !=""){ //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额 
        e.target.value= parseFloat(e.target.value); 
      }
      console.log(e.target.value)
      this.amount = e.target.value
      this.ratioAmount = this.amount*(100-this.dataInfo.ratio)/100 
      this.ratioAmount = this.ratioAmount.toFixed(2)
      if(this.amount&&(this.amount*1<this.dataInfo.balance*1)){
        this.dis = false
      }else{
        this.dis = true
      }
    },
    // 打开输入密码
    submit() {

      if(this.dataInfo.bankCardType==1&&!this.invoiceExpressCompany||!this.invoiceExpressNo||!this.amount){
        Toast('请将信息填写完整');
        return false
      }
      if(this.dataInfo.bankCardType==2&&!this.invoiceExpressNo||!this.amount){
        Toast('请将信息填写完整');
        return false
      }
      if(this.amount<100){
        Toast('提现金额必须大于等于100');
        return false
      }
      if(this.amount>this.dataInfo.balance*1){
        Toast('输入金额大于可提现金额');
        return false
      }
      this.showWmoney = true
    },
    // 点击数字
    onkeyBoard(data) {
      this.pswValue = data
    },
    goBack() {
      this.showWmoney = true
      goBack()
    },
    // 全部提现
    allIn() {
      this.amount = this.dataInfo.balance
      this.ratioAmount = this.amount*(100-this.dataInfo.ratio)/100 
      this.ratioAmount = this.ratioAmount.toFixed(2)
    }
  },
  mounted() {
    getPhone()
    getTokus(this)
    setTimeout(() =>{
      this.getWithdrawInfo()
    },500)
    // this.getWithdrawInfo()
  }  
}
</script>
<style lang="less" scoped>
#withdrawal{
   background-color:#fafafa;
   width: 100%;
    .subBankName{
      text-overflow: -o-ellipsis-lastline;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      line-clamp: 2;
      -webkit-box-orient: vertical;
    }
   .mrsk{
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        background-color: rgba(0,0,0,.5)
    }
   .rule_info{
     height: auto;
     padding: .4rem .3rem .36rem;
      font-family: MicrosoftYaHei;
      font-size: 13px;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 0px;
      .mint-button{
        width: 100%;
        height: .8rem;
        background-color: #1757a8;
        margin-top: .6rem;
      }
      >div{
        line-height: .36rem;
	      color: #999999;
      }
     .title{
      color: #ff0000;
      display: flex;
      align-items: center;
      .ts{
        width: .26rem;
        height: .26rem;
        // background-color: #ff0000;
        border-radius: 50%;
        text-align: center;
        // line-height: .26rem;
        // color: #fff;
        font-size: .18rem;
        margin-right: .1rem;
      }
     }
   }
   .userInfo{
     height: auto;
     background-color: #fff;
     >div{
        width: 100%;
        height: 1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .3rem;
        border-top: .02rem solid #e5e5e5;
        span:nth-of-type(1){
          font-family: MicrosoftYaHei;
          font-size: .3rem;
          font-weight: normal;
          font-stretch: normal;
          letter-spacing: 0px;
          color: #666666;
        }
        span:nth-of-type(2){
          font-family: MicrosoftYaHei;
          font-size: .3rem;
          font-weight: normal;
          font-stretch: normal;
          letter-spacing: 0px;
          color: #010101;
        }
     }
   }
   .express{
     width: 100%;
      height: auto;
      background-color: #fff;
      margin-top: .2rem;
      padding: .3rem;
      >div{
        width: 100%;
        height: 1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        span{
          font-family: MicrosoftYaHei;
          font-size: .3rem;
          font-weight: normal;
          font-stretch: normal;
          letter-spacing: 0px;
          color: #666666;
        }
        input{
          height: 1rem;
          width: 4.74rem;
          border-radius: .1rem;
          border: solid .02rem #e5e5e5;
          font-family: MicrosoftYaHei;
          padding-left: .1rem;
          font-size: .28rem;
          font-weight: normal;
          font-stretch: normal;
          letter-spacing: 0px;
          color: #cccccc;
        }
        input.active{
          color:#000;
        }
      }
      div:nth-of-type(2){
        margin-top: .3rem;
        input{
          height: .8rem;
        }
      }
   }
  .withdrawal_money{
    width: 100%;
    height: 3rem;
    background-color: #fff;
    padding: .4rem .3rem .2rem;
    .amount{
      font-family: MicrosoftYaHei;
      font-size: .28rem;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 0px;
      color: #999999;
      margin-top: .2rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      span{
        color: #1757a8;
      }
      div{
        font-size: .28rem;
        color: #999999;
      }
      .amount2{
        font-size: .28rem;
        color: #999999;
        margin-top: 0;
        height: .38rem;
      }
    }
    .amount1{
      margin-top: .2rem;
      font-family: MicrosoftYaHei;
      font-size: .28rem;
      color: #ff0000;
    }
    div:nth-of-type(2){
        position: relative;
        margin-top: .4rem;
        height: 1.08rem;
        font-size: .36rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #000000;
        input{
            height: 1.08rem;
            border:none;
            border-bottom: 1px solid #e5e5e5;
            padding-left: .42rem;
            font-size: .36rem;
            color: #000000;
        }
        span{
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-54%);
        }
    }
    .title{
        font-family: MicrosoftYaHei;
        font-size: .32rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #333333
    }
    
  }
  .bankInfo{
    width: 100%;
    height: 3.68rem;
    
    padding: .4rem .3rem;  
    .forgetPsw{
        width: 100%;
        text-align: right;
        font-family: MicrosoftYaHei;
        font-size: .26rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #1757a8;
        margin-top: .2rem;
        padding-right: .2rem;
    }
    .bankCard{
        width: 100%;
        height: 2.4rem;
        background-color: #fff;
        display: flex;
        align-items: center;
        box-shadow: 0px .02rem .16rem 0px 
		rgba(204, 204, 204, 0.6);
        border-radius: .1rem;
        padding:0 .2rem;
        .bankCardInfo{
            width: 100%;
            height: 1.2rem;
            display: flex;
            .left{
                width: 1.2rem;
                height: 1.2rem;
                img{
                    width: 1.2rem;
                    height: 1.2rem;
                    display: block;
                    border-radius: 50%;
                    // background-color: #e5e5e5;
                }
            }
            .right{
              flex: 1;
              margin-left: .2rem;
              div:nth-of-type(1){
                font-family: MicrosoftYaHei;
                font-size: .32rem;
                font-weight: normal;
                font-stretch: normal;
                letter-spacing: 0px;
                color: #000000;
              }  
              div:nth-of-type(2){
                font-family: MicrosoftYaHei;
                font-size: .28rem;
                font-weight: normal;
                font-stretch: normal;
                letter-spacing: 0px;  
              }
              div:nth-of-type(3){
                font-family: MicrosoftYaHei;
                font-size: .26rem;
                font-weight: normal;
                font-stretch: normal;
                letter-spacing: 0px;
                color: #666666;    
              }
            }
        }
    } 
  }
}
</style>



