<template>
  <div class="noPsw" :class="showNoPsw?'active':''">
    <div class="text">交易密码不正确</div>   
    <div class="btn">
        <div><router-link to="/changePassword/123">忘记密码</router-link></div>
        <div @click="$emit('cance')">再试一次</div>
    </div>
  </div>
</template>
<script>
export default {
    props:{
        showNoPsw:{
            default:''
        }
    }
}
</script>
<style lang="less" scoped>
.noPsw{
    height: 1.88rem;
    width: 6rem;
    position: fixed;
    background-color: #fff;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) scale(0);
    overflow: hidden;
    border-radius: .1rem;
    transition: all .5s;
    
    .text{
        width: 100%;
        height: .96rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: MicrosoftYaHei;
        font-size: .3rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #000000;
    }
    .btn{
        width: 100%;
        height: .92rem;
        border-top: .02rem solid #e5e5e5;
        display: flex;
        div{
            flex:1;
            font-family: MicrosoftYaHei;
            font-size: .3rem;
            font-weight: normal;
            font-stretch: normal;
            letter-spacing: 0px;
            color: #0081fd;
            text-align: center;
            line-height: .9rem;
        }
        div:nth-of-type(1){
            border-right: 1px solid #e5e5e5;
        }
    }
    
}
.noPsw.active{
    transform: translate(-50%,-50%) scale(1);
    transition: all .5s;
}
</style>



